*{
  font-family: 'Roboto', sans-serif;
}
body{
  margin: 0;
  background: linear-gradient(0deg ,rgb(240, 243, 255)10%,rgb(255, 255, 255)90%);
  height: 100vh;
  overflow: hidden;
  width: 100%;
}
.upper{
  height: 650px;
  background: linear-gradient(45deg ,rgba(228, 231, 245)60%,rgba(246, 245, 247)90%);
  overflow: hidden;
}
.v1{
  float: right;
  margin-right: 50px;
  margin-top:70px;
}
.heading{
  margin-top:30px;
  margin-left: 400px;
  font-size:60px;
}
.lorem{
  margin-left: 400px;
}
.t1{
  color: rgb(116, 116, 116);
  margin-left: 400px;
}
.intro{
  color: orange;
  margin-left: 600px;
  margin-top: -40px;
}
.Learn{
  margin-left: 400px;
  height: 50px;
  border-radius: 35px;
  width: 150px;
  background-color: rgb(93, 56, 224);
  border: none;
  color: white;
  font-weight: 500;
  margin-top: 50px;
  cursor:pointer;
}
.NavbarItems{
  height: 80px;
  display: flex;
  justify-content: left;
  align-items: center;
  font-size: 1.2rem;
}

.navbar-logo{
  color:rgb(0, 0, 0);
  justify-self: start;
  margin-left: 200px;
  cursor:pointer;
}

.nav-menu{
  display: grid;
  grid-template-columns: repeat(6,auto);
  grid-gap:40px;
  text-align: left;
  margin-left: 100px;
  list-style: none;
  font-weight: 500;
  
}
a{text-decoration: none;
  color: black;
}
.bt{
  width: 120px;
  margin-left: 500px;
  padding: 10px;
  border-radius: 25px;
  background-color: black;
  color: white;
  border: none;
  cursor:pointer;
}
.signin{
  margin-left: 10px;
  font-size: 15px;
  font-weight: 500;
}


.left{
  width: 300px;
  height: 150px;
  margin-left: 450px;
  margin-top: 100px;
  background-color: rgb(237, 235, 255);
  float: left;
  border-radius: 5px;
}
.mid{
  width: 300px;
  height: 150px;
  margin-left: 50px;
  margin-top: 50px;
  background-color: rgb(255, 241, 235);
  border-radius: 5px;
  float: left;
}
.right{
  width: 300px;
  height: 150px;
  margin-left: 50px;
  border-radius: 5px;
  margin-top: 100px;
  background-color: rgb(235, 255, 235);
  float: left;
}
.sol{
  color: grey;
  margin-left: 30px;
}
.dat{
  margin-left: 30px;
}

